export default {
  methods: {
    modalConfirm (msg) {
      const confirmModal = this.$root.$children[0].$refs.confirmModal
      confirmModal.openModal(msg)

      return new Promise((resolve, reject) => {
        confirmModal.$on('confirm', () => {
          confirmModal.closeModal()
          resolve()
        })

        confirmModal.$on('cancel', () => {
          confirmModal.closeModal()
          reject(new Error())
        })
      })
    }
  }
}
